<template>
  <div class="container">
    <div class="top">
      <div class="top-left">
        <img src="/static/user/avatar.png" alt="" />
        <div>登录/注册</div>
      </div>
      <div class="top-right">
        <img
          src="/static/user/pic2.png"
          alt=""
          @click="goDetail"
          style="margin-right: 25rpx;"
        />
        <img
          src="/static/user/pic1.png"
          alt=""
          @click="go('/pages/notice/main')"
        />
        <div class="dot">1</div>
      </div>
    </div>
    <div class="card">
      <div class="title">
        我的资产
      </div>
      <div class="content">
        <div class="content-item">
          <div class="main">
            <img src="/static/user/pic3.png" alt="" />
            <span>余额</span>
          </div>
          <div class="text"><span>62.98</span>元</div>
        </div>
        <div class="content-item">
          <div class="main">
            <img src="/static/user/pic5.png" alt="" />
            <span>红包</span>
          </div>
          <div class="text"><span>9</span>个未使用</div>
        </div>
        <div class="content-item">
          <div class="main">
            <img src="/static/user/pic4.png" alt="" />
            <span>优惠券</span>
          </div>
          <div class="text"><span>19</span>张未使用</div>
        </div>
        <div class="content-item">
          <div class="main">
            <img src="/static/user/pic6.png" alt="" />
            <span>佣金</span>
          </div>
          <div class="text"><span>1982</span>元</div>
        </div>
      </div>
    </div>
    <div class="card">
      <div class="title">
        我的资产
      </div>
      <div class="content-2">
        <div class="content-item-2">
          <img
            src="/static/user/pic7.png"
            alt=""
            style="height: 44rpx; width: 36rpx;"
          />
          <span>余额</span>
        </div>
        <div class="content-item-2">
          <img src="/static/user/pic8.png" alt="" />
          <span>收藏的店</span>
        </div>
        <div class="content-item-2">
          <img src="/static/user/pic9.png" alt="" />
          <span>邀请赚钱</span>
        </div>
      </div>
    </div>
    <div class="card">
      <div class="title">
        我的服务
      </div>
      <div class="content-2">
        <div class="content-item-2">
          <img
            src="/static/user/pic15.png"
            alt=""
            style="height: 44rpx; width: 36rpx;"
          />
          <span>我的积分</span>
        </div>
        <div class="content-item-2">
          <img src="/static/user/pic10.png" alt="" />
          <span>积分兑换</span>
        </div>
        <div class="content-item-2">
          <img src="/static/user/pic11.png" alt="" />
          <span>我要合作</span>
        </div>
        <div class="content-item-2">
          <img src="/static/user/pic12.png" alt="" />
          <span>联系客服</span>
        </div>
      </div>
    </div>
    <div class="card">
      <div class="title">
        更多推荐
      </div>
      <div class="content-2">
        <div class="content-item-2">
          <img
            src="/static/user/pic13.png"
            alt=""
            style="height: 44rpx; width: 36rpx;"
          />
          <span>关于我们</span>
        </div>
        <div class="content-item-2">
          <img src="/static/user/pic14.png" alt="" />
          <span>用户协议</span>
        </div>
        <div class="content-item-2">
          <img src="/static/user/pic16.png" alt="" />
          <span>隐私政策</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      itemList: [
        {
          title: "美团红包",
          icon: "mt-red-packet-o",
          path: "/pages/redPacket/main",
          amount: 4,
        },
        {
          title: "商家代金券",
          icon: "mt-coupon-o",
          path: "/pages/couponList/main",
          amount: 10,
        },
        {
          title: "我的地址",
          icon: "mt-my-location-o",
          path: "/pages/addressList/main",
        },
        {
          title: "邀请有奖",
          icon: "mt-gift-o",
        },
        {
          title: "客服中心",
          icon: "mt-customer-service-o",
        },
        {
          title: "帮助和反馈",
          icon: "mt-help-o",
          path: "/pages/feedback/main",
        },
        {
          title: "协议和说明",
          icon: "mt-protocol-o",
          path: "/pages/protocol/main",
        },
      ],
    };
  },
  computed: {},
  methods: {
    goDetail() {
      wx.navigateTo({ url: "/pages/meMessage/main" });
    },
    go(url) {
      wx.navigateTo({ url });
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  height: 100vh;
  padding: 0 24rpx;
  .top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 50rpx;
    .top-left {
      display: flex;
      justify-content: center;
      align-items: center;
      img {
        width: 124rpx;
        height: 124rpx;
      }
      div {
        margin-left: 20rpx;
        font-size: 36rpx;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: rgba(0, 0, 0, 1);
      }
    }
    .top-right {
      position: relative;
      img {
        width: 40rpx;
        height: 40rpx;
      }
      .dot {
        width: 24rpx;
        height: 24rpx;
        border-radius: 50%;
        background: rgba(255, 111, 99, 1);
        color: white;
        font-size: 24rpx;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        right: 0;
        top: -8rpx;
      }
    }
  }
  .card {
    min-height: 146rpx;
    background: rgba(255, 255, 255, 1);
    border-radius: 10px;
    margin-top: 24rpx;
    .title {
      font-size: 28rpx;
      font-family: PingFangSC-Medium, PingFang SC;
      font-weight: 500;
      color: rgba(0, 0, 0, 1);
      margin-top: 24rpx;
      margin-left: 24rpx;
    }
    .content {
      display: flex;
      justify-content: space-around;
      align-items: center;
      padding: 0 48rpx;
      margin-top: 24rpx;
      padding-bottom: 54rpx;
      .content-item {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .main {
          display: flex;
          justify-content: center;
          align-items: center;
          span {
            font-size: 28rpx;
            font-family: PingFangSC-Regular, PingFang SC;
            font-weight: 400;
            color: rgba(0, 0, 0, 1);
          }
          img {
            width: 40rpx;
            height: 40rpx;
            margin-right: 4px;
          }
        }

        .text {
          font-size: 24rpx;
          font-family: PingFangSC-Semibold, PingFang SC;
          font-weight: 500;
          color: rgba(153, 153, 153, 1);
          margin-top: 24rpx;
          span {
            font-size: 28rpx;
            font-family: PingFangSC-Semibold, PingFang SC;
            font-weight: 500;
            color: rgba(255, 74, 38, 1);
          }
        }
      }
    }
    .content-2 {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 0 48rpx;
      margin-top: 24rpx;
      padding-bottom: 54rpx;
      .content-item-2 {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        width: 25%;
        img {
          width: 44rpx;
          height: 44rpx;
        }
        span {
          font-size: 24rpx;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: rgba(0, 0, 0, 1);
          margin-top: 24rpx;
        }
      }
    }
  }
}
</style>



